import React, { Component } from 'react'
import '../css/reminders.css'
import AnimateHeight from 'react-animate-height';


import JSON from './reminders.json';


class Reminders extends Component{

   constructor(){
      super()
      this.state = {
         time:'12:45',
         ReminderJSON : JSON,
         colors:['#999999','#E4BEA8','#FAE07A','#EEA2AD','#CCFFCC','#B266B2'],
         inputBox:'0px'
      }

      this.ReminderList = this.state.ReminderJSON.map((item , index ) => {
         var id = item.id
         var time = item.time
         var instance = item.instance
         var date = item.date
         var message = item.message.slice(0,20) + '....'
         var colour = item.colour
         var alarm = item.alarm


         var alarmOn = 'grey'
         if (alarm === 'yes'){alarmOn = '#404040'}
         return (
            <div key = {id} style = {{backgroundColor:`${colour}`}} className = "reminder">
                      <div className = "reminder-info">
                         <p className = "time"> {time} <span>{instance}</span></p> 
                        <p className = "date">{date}</p>
                        <p className = "message">{message}</p>
                      </div>
                      <div className = "reminder-tools">
                         <div>
                              <i className="fas fa-trash"></i>
                              <i style = {{color:`${alarmOn}`}} className="fas fa-bell"></i>
                         </div>
                              <div className = 'colour-changer'></div>
                      
                      </div>     
                   </div>

         )  // End of return
      }); //End of Map
      



      this.showInput = () => {
      
         if(this.state.inputBox === '0px'){
            this.setState({
               inputBox:"70%"
            })
         } else if (this.state.inputBox === '70%'){
            this.setState({
               inputBox:'0px'
            })
         }
           
      }
      
         
      this.randomColor = this.state.colors.map((color , index) => {
            return ( <div key = {index} style = {{backgroundColor:`${color}`}} className = 'colorCircle' ></div> )
         })
   
   



   } //End of constructor
  
   




   render(){
    
      
      return(
         
         <div className ="holder">
               <div className = "circle">
            <div onClick = {(event) => {this.showInput()}} className = "circle-add"><i className="fi-xwluxl-plus-wide"></i></div>
           
             </div>




             <div style = {{maxHeight:`${this.state.inputBox}`} }className = "new-reminder" >

                
                 <div className='timeSelector'>
                              <div className="hourSelector">
                                 <i className="fi-xnsuxl-chevron-solid"></i>
                                 <div className="timeBox"></div>
                                 <i className="fi-xnsdxl-chevron-solid"></i>
                              </div>
                              <div className="hourSelector">
                                 <i className="fi-xnsuxl-chevron-solid"></i>
                                 <div className="timeBox"></div>
                                 <i className="fi-xnsdxl-chevron-solid"></i>
                              </div>
                              <div className="hourSelector">
                                 <i className="fi-xnsuxl-chevron-solid"></i>
                                 <div className="timeBox"></div>
                                 <i className="fi-xnsdxl-chevron-solid"></i>
                                 </div>
                  </div>
                  <div className = 'dateSelector'>
                                 <div className="hourSelector">
                                    <i className="fi-xnsuxl-chevron-solid"></i>
                                    <div className="timeBox"></div>
                                    <i className="fi-xnsdxl-chevron-solid"></i>
                                 </div>
                                 <div className="hourSelector">
                                    <i className="fi-xnsuxl-chevron-solid"></i>
                                    <div className="monthBox"></div>
                                    <i className="fi-xnsdxl-chevron-solid"></i>
                                 </div>
                                 <div className="hourSelector">
                                    <i className="fi-xnsuxl-chevron-solid"></i>
                                    <div className="yearBox"></div>
                                    <i className="fi-xnsdxl-chevron-solid"></i>
                                 </div>
                  </div>
                  <div className="alarmBox">
                         <p className = "switchText" >Ding Dong ?</p>
                        <input id="switchcheckbox" type="checkbox" className="hidden" />
                        
                           <label htmlFor="switchcheckbox" id="switch"></label>
                  </div>
                  <div className="colorBox">
                     {this.randomColor}
                  </div>
                  <div className = 'saveButton' >
                     SAVE
                  </div>
               </div>
            
            <div className = "reminder-holder">
                  {this.ReminderList}
                           
             </div>
         </div>
         
      )
   }
}

export default Reminders